    // Catch offcanvasmenu triggers
    $('.sb-toggle-right-offcanvasmenu').on('click', function(event) {
        event.stopPropagation();
        event.preventDefault();
        if (mySlidebars.slidebars.active('right')) { // Check if the right Slidebar is active.
            mySlidebars.slidebars.close(); // It is active, close the right Slidebar.
            setTimeout(function() { // Set a timeout function to hide the content after the Slidebar is closed.
                $('#offcanvasmenu, #profilemenu').css('display', 'none');
                $( '.hamburger_layer' ).removeClass('hamburger_icon--to-arrow');
            }, 300);
        } else {
            $('#offcanvasmenu').css('display', 'block'); // Show the desired content.
            mySlidebars.slidebars.open('right'); // Open the right Slidebar.
            $( '.hamburger_layer' ).addClass('hamburger_icon--to-arrow');
        }
    });

    // Catch profilemenu triggers
