    $("select.fancyselect").select2();

    $("select[name='volunteer_for_company']").select2({
        templateResult: formatCompany,
        templateSelection: formatSelectedCompany,
        matcher: function (params, data) {
          if ($.trim(params.term) === '') {
            return data;
          }

          if (data.text.toString().toLowerCase().indexOf(params.term.toLowerCase()) > -1) {
            return $.extend({}, data, true);
          }

          return null;
        }
    });

    function formatSelectedCompany (company) {
        if(!$.isArray(company.text)){
            company.text = company.text.split('\n');
        } 
        return company.text[0];
    };

    function formatCompany (company) {
        if (!company.id) { return company.text; }

        if(!$.isArray(company.text)){
            company.text = company.text.split('\n');
        } 
        company = $(
            '<span class="select2_option-title"> ' + company.text[0] + '</span><span>' + company.text[1] + '</span>'
        );

        return company;     
    };

